.profile {
    width: 322px;
    height: 322px;
    background-image: var(--profile-img);
    background-repeat: no-repeat;
    background-size: 100%;

}

.home-bio{
    max-width: 360px;
}

.home-bio #btn-portfolio {
    background-color: var(--color-purple);
    border: none;
    color: var(--color-text-projects);
}

.home-bio #btn-formation {
    background-color: transparent;
    color: var(--color-pink);
    
    border: 1px solid var(--color-pink);
}

.home-bio button{
    width: 152px;
    height: 35px;
    border-radius: 6px;
    font-size: 12px;
    letter-spacing: 0.15em;
}


@media screen and (min-width: 992px) {

    .home{
        margin-top: 4rem ;
        margin-bottom: 4rem ;
        display: flex;
        flex-direction: row !important;
    }

    .home-bio{
        vertical-align: baseline;
        text-align: left !important;
    }

    .home-buttons{
        display: flex;
        flex-direction: row;
    }
    
}

